import { jwtDecode } from 'jwt-decode';
import { gapi } from 'gapi-script';

const CLIENT_ID = process.env.REACT_APP_GOOGLE_CLIENT_ID;
const API_KEY = process.env.REACT_APP_GOOGLE_API_KEY;
// Tasks APIのスコープ
const SCOPES = 'https://www.googleapis.com/auth/tasks https://www.googleapis.com/auth/tasks.readonly';
const DISCOVERY_DOCS = ['https://www.googleapis.com/discovery/v1/apis/tasks/v1/rest'];

// ローカルストレージのキー
const TOKEN_KEY = 'google_auth_token';
const ACCESS_TOKEN_KEY = 'google_access_token';
const USER_KEY = 'google_user_info';

// Google Identity Services (GIS) の初期化
export const initGoogleAuth = () => {
  return new Promise((resolve, reject) => {
    if (!CLIENT_ID) {
      reject(new Error('Google Client ID is not configured'));
      return;
    }

    // すでにスクリプトが読み込まれているか確認
    if (window.google && window.google.accounts && window.google.accounts.id) {
      initializeGoogleAuth(resolve, reject);
      return;
    }

    // GISの初期化（ログインボタン用）
    const script = document.createElement('script');
    script.src = 'https://accounts.google.com/gsi/client';
    script.async = true;
    script.defer = true;
    script.onload = () => {
      try {
        initializeGoogleAuth(resolve, reject);
        
        // カスタムイベントを発火してボタンのレンダリングを通知
        const event = new CustomEvent('google-loaded');
        window.dispatchEvent(event);
      } catch (error) {
        reject(error);
      }
    };
    script.onerror = () => {
      reject(new Error('Google API script failed to load'));
    };
    document.body.appendChild(script);
  });
};

// Google Identity Servicesの初期化関数
const initializeGoogleAuth = (resolve, reject) => {
  try {
    window.google.accounts.id.initialize({
      client_id: CLIENT_ID,
      callback: handleCredentialResponse,
      auto_select: false,
      cancel_on_tap_outside: true,
      prompt_parent_id: 'googleButtonContainer', // ボタンの親要素のID
      state_cookie_domain: window.location.hostname, // 現在のドメインに制限
      ux_mode: 'popup', // ポップアップモードを明示的に指定
    });
    
    // GAPI（Google API Client）の初期化
    initGapiClient().then(() => {
      resolve();
    }).catch(error => {
      console.error('Failed to initialize GAPI client:', error);
      // GISの初期化は成功したとみなす
      resolve();
    });
  } catch (error) {
    reject(error);
  }
};

// GAPI（Google API Client）の初期化
const initGapiClient = () => {
  return new Promise((resolve, reject) => {
    try {
      // GAPIスクリプトの読み込み
      const gapiScript = document.createElement('script');
      gapiScript.src = 'https://apis.google.com/js/api.js';
      gapiScript.async = true;
      gapiScript.defer = true;
      gapiScript.onload = () => {
        gapi.load('client', async () => {
          try {
            await gapi.client.init({
              apiKey: API_KEY,
              discoveryDocs: DISCOVERY_DOCS,
            });
            resolve();
          } catch (error) {
            reject(error);
          }
        });
      };
      gapiScript.onerror = () => {
        reject(new Error('GAPI script failed to load'));
      };
      document.body.appendChild(gapiScript);
    } catch (error) {
      reject(error);
    }
  });
};

// Google認証レスポンスのハンドラー
const handleCredentialResponse = (response) => {
  if (response && response.credential) {
    try {
      // JWTをデコード
      const decodedToken = jwtDecode(response.credential);
      
      // トークンとユーザー情報を保存
      localStorage.setItem(TOKEN_KEY, response.credential);
      localStorage.setItem(USER_KEY, JSON.stringify({
        id: decodedToken.sub,
        name: decodedToken.name,
        email: decodedToken.email,
        picture: decodedToken.picture
      }));
      
      // アクセストークンとしてもJWTを保存（Tasks APIで使用するため）
      localStorage.setItem(ACCESS_TOKEN_KEY, response.credential);
      sessionStorage.setItem(ACCESS_TOKEN_KEY, response.credential);
      console.log('JWT token saved as access token');
      
      // 認証状態変更イベントを発火
      const event = new CustomEvent('googleAuthStateChanged', { 
        detail: { isAuthenticated: true } 
      });
      window.dispatchEvent(event);
      
      // ページをリロード
      window.location.reload();
    } catch (error) {
      console.error('Error processing credential response:', error);
    }
  }
};

// 認証状態のチェック
export const isAuthenticated = () => {
  try {
    const token = localStorage.getItem(TOKEN_KEY) || sessionStorage.getItem(TOKEN_KEY);
    const accessToken = localStorage.getItem(ACCESS_TOKEN_KEY) || sessionStorage.getItem(ACCESS_TOKEN_KEY);
    
    if (token) {
      try {
        const decodedToken = jwtDecode(token);
        const currentTime = Date.now() / 1000;
        
        // トークンの有効期限をチェック
        if (decodedToken.exp && decodedToken.exp > currentTime) {
          return true;
        }
      } catch (e) {
        console.error('Error decoding token:', e);
      }
    }
    
    // アクセストークンがある場合も認証済みとみなす
    if (accessToken) {
      return true;
    }
    
    return false;
  } catch (e) {
    console.error('Error checking authentication status:', e);
    return false;
  }
};

// ユーザー情報の取得
export const getUserInfo = () => {
  try {
    const userInfo = localStorage.getItem(USER_KEY);
    return userInfo ? JSON.parse(userInfo) : null;
  } catch (e) {
    console.error('Error getting user info:', e);
    return null;
  }
};

// サインイン
export const signIn = (options = {}) => {
  try {
    console.log('Starting OAuth2 sign in flow');
    
    // リダイレクトURI（現在のページのURL）
    const redirectUri = window.location.origin;
    console.log('Redirect URI:', redirectUri);
    
    // クライアントIDの確認
    if (!CLIENT_ID) {
      console.error('Google Client ID is not configured');
      throw new Error('Google Client ID is not configured');
    }
    console.log('Client ID available:', !!CLIENT_ID);
    
    // モバイルデバイスかどうかを検出
    const isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
    console.log('Device detection in signIn:', isMobile ? 'Mobile' : 'Desktop');
    
    // OAuth 2.0認証URLを構築
    const authUrl = `https://accounts.google.com/o/oauth2/v2/auth?client_id=${CLIENT_ID}&redirect_uri=${encodeURIComponent(redirectUri)}&response_type=token&scope=${encodeURIComponent(SCOPES)}&prompt=select_account&include_granted_scopes=true`;
    console.log('Auth URL:', authUrl);
    
    // 認証前に現在のタイムスタンプをセッションストレージに保存（デバッグ用）
    sessionStorage.setItem('auth_request_time', Date.now().toString());
    
    // ポップアップウィンドウで開くかどうか
    if (options.usePopup) {
      console.log('Opening auth URL in popup window');
      
      // ポップアップウィンドウのサイズと位置を計算
      const width = 500;
      const height = 600;
      const left = window.screenX + (window.outerWidth - width) / 2;
      const top = window.screenY + (window.outerHeight - height) / 2.5;
      
      // ポップアップウィンドウを開く
      const popup = window.open(
        authUrl,
        'googleAuthPopup',
        `width=${width},height=${height},left=${left},top=${top},resizable=yes,scrollbars=yes,status=yes`
      );
      
      // ポップアップウィンドウが閉じられたかどうかを監視
      if (popup) {
        const checkPopupClosed = setInterval(() => {
          if (popup.closed) {
            clearInterval(checkPopupClosed);
            console.log('Auth popup closed');
            
            // アクセストークンを確認
            const token = getAccessToken();
            if (token) {
              console.log('Access token found after popup closed');
              // 認証状態変更イベントを発火
              const event = new CustomEvent('googleAuthStateChanged', { 
                detail: { isAuthenticated: true } 
              });
              window.dispatchEvent(event);
              
              // ページをリロード
              window.location.reload();
            }
          }
        }, 1000);
      } else {
        console.error('Failed to open popup window');
        throw new Error('ポップアップウィンドウを開けませんでした。ポップアップブロッカーを無効にしてください。');
      }
    } else {
      // 通常のリダイレクト
      console.log('Redirecting to auth URL');
      window.location.href = authUrl;
    }
  } catch (error) {
    console.error('Error during sign in:', error);
    throw error;
  }
};

// サインアウト
export const signOut = () => {
  try {
    // ローカルストレージからトークンとユーザー情報を削除
    localStorage.removeItem(TOKEN_KEY);
    localStorage.removeItem(USER_KEY);
    localStorage.removeItem(ACCESS_TOKEN_KEY);
    
    // セッションストレージからも削除
    sessionStorage.removeItem(TOKEN_KEY);
    sessionStorage.removeItem(ACCESS_TOKEN_KEY);
    
    // Google Identity Servicesのサインアウト
    if (window.google && window.google.accounts && window.google.accounts.id) {
      window.google.accounts.id.disableAutoSelect();
    }
    
    // 認証状態変更イベントを発火
    const event = new CustomEvent('googleAuthStateChanged', { 
      detail: { isAuthenticated: false } 
    });
    window.dispatchEvent(event);
    
    // ページをリロード
    window.location.reload();
  } catch (error) {
    console.error('Error during sign out:', error);
    throw error;
  }
};

// 認証状態変更リスナーの追加
export const addAuthStateListener = (callback) => {
  window.addEventListener('googleAuthStateChanged', (event) => {
    callback(event.detail.isAuthenticated);
  });
};

// 認証状態変更リスナーの削除
export const removeAuthStateListener = (callback) => {
  window.removeEventListener('googleAuthStateChanged', callback);
};

// アクセストークンの取得
export const getAccessToken = () => {
  try {
    console.log('Getting access token');
    
    // デバッグ情報を記録
    const debugInfo = {
      url: window.location.href,
      hash: window.location.hash,
      hasLocalStorage: typeof localStorage !== 'undefined',
      hasSessionStorage: typeof sessionStorage !== 'undefined',
      userAgent: navigator.userAgent,
      platform: navigator.platform,
      isMobile: /iPhone|iPad|iPod|Android/i.test(navigator.userAgent)
    };
    console.log('Access token debug info:', debugInfo);
    
    // URLハッシュからアクセストークンを取得
    const hash = window.location.hash;
    if (hash && hash.includes('access_token=')) {
      try {
        const accessToken = hash.match(/access_token=([^&]*)/)[1];
        if (accessToken) {
          console.log('Access token found in URL hash');
          
          try {
            // アクセストークンをローカルストレージに保存
            localStorage.setItem(ACCESS_TOKEN_KEY, accessToken);
            console.log('Access token saved to localStorage');
          } catch (storageError) {
            console.error('Error saving to localStorage:', storageError);
          }
          
          try {
            // セッションストレージにも保存
            sessionStorage.setItem(ACCESS_TOKEN_KEY, accessToken);
            console.log('Access token saved to sessionStorage');
          } catch (sessionError) {
            console.error('Error saving to sessionStorage:', sessionError);
          }
          
          // Cookieにも保存（サードパーティCookie制限の回避策）
          try {
            document.cookie = `${ACCESS_TOKEN_KEY}=${accessToken}; path=/; max-age=3600; SameSite=Strict`;
            console.log('Access token saved to cookie');
          } catch (cookieError) {
            console.error('Error saving to cookie:', cookieError);
          }
          
          // URLハッシュをクリア
          try {
            window.history.replaceState(null, null, window.location.pathname);
            console.log('URL hash cleared');
          } catch (historyError) {
            console.error('Error clearing URL hash:', historyError);
          }
          
          return accessToken;
        }
      } catch (e) {
        console.error('Error extracting access token from hash:', e);
      }
    }
    
    // 各種ストレージからアクセストークンを取得
    let accessToken = null;
    
    // ローカルストレージから取得
    try {
      accessToken = localStorage.getItem(ACCESS_TOKEN_KEY);
      if (accessToken) {
        console.log('Access token found in localStorage');
        return accessToken;
      }
    } catch (e) {
      console.error('Error reading from localStorage:', e);
    }
    
    // セッションストレージから取得
    try {
      accessToken = sessionStorage.getItem(ACCESS_TOKEN_KEY);
      if (accessToken) {
        console.log('Access token found in sessionStorage');
        return accessToken;
      }
    } catch (e) {
      console.error('Error reading from sessionStorage:', e);
    }
    
    // Cookieから取得
    try {
      const cookies = document.cookie.split(';');
      for (let i = 0; i < cookies.length; i++) {
        const cookie = cookies[i].trim();
        if (cookie.startsWith(ACCESS_TOKEN_KEY + '=')) {
          accessToken = cookie.substring(ACCESS_TOKEN_KEY.length + 1);
          console.log('Access token found in cookie');
          return accessToken;
        }
      }
    } catch (e) {
      console.error('Error reading from cookie:', e);
    }
    
    console.log('No access token found in any storage');
    return null;
  } catch (e) {
    console.error('Error getting access token:', e);
    return null;
  }
};
// 明示的なスコープ承認を要求
export const requestTasksScope = async () => {
  try {
    console.log('Requesting tasks scope explicitly');
    
    // 現在のアクセストークンを取得
    const currentToken = getAccessToken();
    if (!currentToken) {
      console.error('No access token available');
      return false;
    }
    
    // 強制的に再認証を行う
    localStorage.removeItem(ACCESS_TOKEN_KEY);
    localStorage.removeItem(TOKEN_KEY);
    
    // Google OAuth 2.0認証ページにリダイレクト
    const redirectUri = window.location.origin;
    const authUrl = `https://accounts.google.com/o/oauth2/v2/auth?client_id=${CLIENT_ID}&redirect_uri=${encodeURIComponent(redirectUri)}&response_type=token&scope=${encodeURIComponent(SCOPES)}&prompt=select_account&include_granted_scopes=true`;
    
    console.log('Redirecting to auth URL:', authUrl);
    window.location.href = authUrl;
    
    return true;
  } catch (error) {
    console.error('Failed to request tasks scope:', error);
    return false;
  }
};
